*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  scroll-behavior: smooth;
  
}
body{
  background: #000000;
}
a{
  text-decoration: none;
  color: #ffffff;
}
.navbar{
  width: 100%;
  height: 10vh;
  background-color: none;
  position: fixed;
  top: 0;
  box-shadow: 0px 0px 14px 5px rgba(0,0,0,0.54);
  border-radius: 3px;
  z-index: 1000;
}
.nav-links{
  display: flex;
  background-color: transparent;
  align-items:center;
  justify-content: flex-end;
  width:100%;
  height: 100%;
}
.nav-link{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15%;
  height: 100%;
  font-size: 15px;
  font-weight: 500;
}
.logo-link{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 5rem !important;
  margin-right: auto;
}
.logo-img{
  width: 100%;
  height: 100%;
 
}
.logo-img img{
  object-fit: contain;
  width: 100%;
  height: 100%;
  
}
/* MAIN */
#main{
  margin-top: 10vh;
  background-image: url(./images/jarras.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 90vh;
  position: relative;
  overflow: hidden;
  background-color: black;
}

.text{
  position: absolute;
  top: 30%;
  right: 2%;
  font-size: 55px;
  font-weight: 400;
  width: 40%;
  height: 80%;
  color:#FCA311;
}
.p-text{
  color: #ffffff;
  position: absolute;
  font-size: 22px;
  top: 50%;
  right: 11%;
}
#localizacion{
  margin-top: 10vh;
  height: 100vh;
  width: 100%;
  color: #ffffff;
  background-image: url(./images/negra.jpg);
  background-size: cover;
  background-position-y: 5vh;
  background-position-x: -13vw;
  background-attachment: fixed;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden;
}
.h1-localizacion{
  position: absolute;
  top: 30%;
  right: 2%;
  font-size: 55px;
  font-weight: 400;
  width: 40%;
  height: 80%;
  color:#FCA311;
}
.a-localizacion{
  color: #ffffff;
  position: absolute;
  font-size: 22px;
  top: 45%;
  right: 7%;width: 35%;
}
.map{
  position: absolute;
  top: 60%;
  right: 15%;
  width: 25%;
  height: 35%;
}
#nosotros{
  margin-top: 10vh;
  width: 100%;
  height: 90vh;
  position: relative;
}

#nosotros1{
  
  width: 100%;
  height: 45vh;
  background-image: url(./images/grifos.jpg);
  background-size: 30%;
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: 5%;
  
  
}
.h1-nosotros1{
  position: absolute;
  color: #ffffff;
  font-size: 22px;
  top: 20%;
  right: 20%;
}
#nosotros2{
  width: 100%;
  height: 45vh;
  background-image: url(./images/latas.jpg);
  background-size: 30%;
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: 95%; 
}
.h1-nosotros2{
  position: absolute;
  color: #ffffff;
  width: 30%;
  font-size: 22px;
  top: 80%;
  left: 15%;
}
#carta{
  margin-top: 10vh;
  width: 100%;
  height: 90vh;
  position: relative;
}
#carta1{
  width: 100%;
  height: 45vh;
  background-image: url(./images/industriales.jpg);
  background-size: 30%;
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: 5%;
  
}
.h1-carta1{
  position: absolute;
  color: #ffffff;
  font-size: 22px;
  top: 20%;
  right: 20%;
}
.span-carta{
  color:#FCA311
}
#carta2{
  width: 100%;
  height: 45vh;
  background-image: url(./images/artesanas.jpg);
  background-size: 30%;
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: 95%; 
}
.h1-carta2{
  position: absolute;
  color: #ffffff;
  font-size: 22px;
  width: 25%;
  top: 80%;
  left: 15%;
}

#horario{
  margin-top: 10vh;
  width: 100%;
  height: 55vh;
  position: relative;
}
#horario1{
  width: 100%;
  height: 45vh;
  background-image: url(./images/horario.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-position-x: 5%;
  background-size: contain  ;
}
.h1-horario{
  position: absolute;
  color: #FCA311;
  font-size: 55px;
  font-weight: 400;
  top: 30%;
  right: 20%;
}
#horario2{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 10vh;
  margin-top: 5vh;
  background-color: rgb(22, 22, 22);
}
.a-icons{
  display: flex;
  justify-content: center;
  align-items: center;
}
.instagram-icon{
  width: 10%;
  height: 50%;
  color: #FCA311;
}
.facebook-icon{
  width: 10%;
  height: 50%;
  color: #FCA311;
}
.nav-button{
  display: none;
}
.hidden{
  display: none;
}
.show{
  display: none;
}

/* MOBILE */

@media screen and (max-width: 480px) {
  .nav-links{
    justify-content: center;
  }
  .nav-link {
    display: none;
  }
  .logo-link{
    margin: 0;
  }
  .nav-menu{
    position: absolute;
    right: 10%;
    z-index: 1001;
    list-style: none;
  }
  .nav-button{
    display: block;
    border: none;
    background: none;
    color: white;
    z-index: 1000;
  }
  .hidden{
    display: none;
  }
  .show{
    position: absolute;
    top: 2vh;
    width: 90%;
    left: 5%;
    height: 95vh;
    background: rgba(0, 0, 0, 0.651);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    font-size: 25px;
  }
  
  .text{
    top: 20%;
    left: 10%;
  }
  .p-text{
    left: 10%;
  }
  #localizacion{
    background-size: 300%;
    background-position: center;
    background-position-x: 25%;
  }
  .h1-localizacion{
    top: 20%;
    left: 10%;
    font-size: 30px;
  }
  .a-localizacion{
    display: none;
  }
  .map{
    top: 40%;
    left: 10%;
    width: 60%;
  }
  #nosotros{
    height: 200vh;
  }
  #nosotros1{
    height: 90vh;
    background-size: contain;
    background-position-x: center;
  }
  .h1-nosotros1{
    top: 40%;
    left: 10%;
  }
  #nosotros2{
    height: 90vh;
    background-size: contain;
    background-position-x: center;
  }
  .h1-nosotros2{
    top: 85%;
    width: 80%;
    left: 10%;
  }
  #carta{
    height: 200vh;
  }
  #carta1{
    height: 90vh;
    background-size: contain;
    background-position-x: center;
  }
  .h1-carta1{
    top: 46%;
    left: 10%;
    width: 80%;
  }
  #carta2{
    height: 90vh;
    background-size: contain;
    background-position-x: center;
  }
  .h1-carta2{
    top: 85%;
    width: 80%;
    left: 10%;
  }
  #horario{
    height: 65vh;
  }
  #horario1{
    height: 55vh;
    background-position-x: center;
  }
  .h1-horario{
    display: none;
  }


}
@media screen and (min-width:481px) and (max-width:820px) {
  .text{
    top: 30%;
    left: 10%;
  }
  .p-text{
    left: 10%;
  }
  #localizacion{
    background-size: 200%;
    background-position: center;
    background-position-x: 25%;
  }
  .h1-localizacion{
    top: 20%;
    left: 10%;
    
  }
  .a-localizacion{
    top: 35%;
    left: 10%;
    width: 50%;
  }
  .map{
    top: 45%;
    left: 10%;
    width: 50%;
  }
  
  #nosotros1{
    background-size: 50%;
    
  }
  .h1-nosotros1{
    top: 20%;
    right: 10%;
    width: 30%;
  }
  #nosotros2{
    background-size: 50%;
  }
  .h1-nosotros2{
    top: 70%;
    left: 10%;
    width: 30%;
  }
  #carta1{
    
    background-size: contain;
  }
  .h1-carta1{
    top: 20%;
   right: 10%;
    width: 30%;
  }
  #carta2{
    background-size: 50%;
  }
  .h1-carta2{
    top: 70%;
    width: 30%;
    left: 10%;
  }
  #horario{
    height: 65vh;
  }
  #horario1{
    height: 55vh;
    background-position-x: center;
  }
  .h1-horario{
    display: none;
  }

}
